html {
  font: 16px/1.618 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif; }

table {
  width: 100%; }
  table th {
    text-align: left;
    border-bottom: 1px solid black; }
  table td {
    border-bottom: 1px solid gray;
    padding: 10px 0; }

html {
  height: 100%; }
  html body {
    height: 100%;
    display: flex; }

body > nav {
  width: 240px;
  flex: 0 0 240px;
  background-color: #252d3a; }
  body > nav h1 {
    margin: 0;
    padding: 20px 30px;
    border-bottom: 1px solid #2c3441;
    font-weight: 300;
    color: #24a79f;
    text-transform: uppercase;
    text-align: center; }
  body > nav ul {
    margin: 0;
    padding: 0; }
    body > nav ul li a {
      width: 100%;
      height: 54px;
      line-height: 54px;
      padding: 0 30px;
      display: block;
      box-sizing: border-box;
      border-left: 3px solid #212530;
      border-bottom: 1px solid #2c3441;
      text-decoration: none;
      color: #878d99; }
      body > nav ul li a:hover {
        color: #e8edf0; }
      body > nav ul li a.active {
        border-left-color: #24a79f;
        color: #e8edf0; }

body > main > header {
  height: 50px;
  background-color: #ffffff;
  font-size: .5rem;
  position: relative;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3);
  z-index: 100; }

body > main {
  flex: 1 0 auto;
  background-color: #e7ebee; }

section.list > header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 20px 20px 20px;
  background-color: #ffffff;
  position: relative;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3);
  z-index: 1; }
  section.list > header h1 {
    margin: 0;
    font-weight: 300;
    color: #141414; }

section.list table {
  width: 100%; }
  section.list table tr:hover td {
    background-color: #f0f0f0; }
  section.list table tr td, section.list table tr th {
    height: 48px;
    line-height: 48px;
    padding: 0 30px;
    background-color: #ffffff;
    border-bottom: 1px solid #e1e1e1; }
  section.list table tr th {
    text-align: left;
    font-weight: 500;
    color: #282828; }
  section.list table tr td {
    color: #808080;
    cursor: pointer; }
